import React, { Component } from 'react';

import './index.css';

export default class PageHead extends Component {

    back = () => {
        this.props.history.goBack();
    }

    render() {
        return (
            <div className="page-main-head">
                {this.props.goback ? (<a href="javascript:;" className="btn-goback" onClick={this.back}>&lt;返回</a>) : null}
                <div className="page-main-title">
                    {this.props.children}
                </div>
            </div>
        );
    }
}