import React, { Component } from 'react';
import PropTypes from "prop-types";
import E from 'wangeditor';

import './index.css';

export default class RichEditor extends Component {

    static defaultProps = {
        value: ''
    }

    static propTypes = {
        type: PropTypes.string,
        value: PropTypes.string,
        onChange: PropTypes.func,
        className: PropTypes.string,
        style: PropTypes.object,
        uploadPath: PropTypes.string,
        onImageReceive: PropTypes.func
    }

    componentDidMount() {
        const toolbox = this.refs.toolbox;
        const content = this.refs.content;
        this.editor = new E(toolbox, content);
        // 使用 onchange 函数监听内容的变化，并实时更新到 state 中
        this.editor.customConfig.onchange = html => {
            if (this.props.onChange) {
                this.props.onChange(html);
            }
        };
        this.editor.customConfig.zIndex = 1;
        if (this.props.uploadPath) {
            this.editor.customConfig.uploadImgServer = this.props.uploadPath;
            this.editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024;
            this.editor.customConfig.uploadImgMaxLength = 1;
            if (this.props.onImageReceive) {
                this.editor.customConfig.uploadImgHooks = {
                    customInsert: this.props.onImageReceive
                }
            }
        }

        this.editor.create();
        this.editor.txt.html(this.props.value);
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.value !== nextProps.value) {
            this.editor.txt.html(nextProps.value);
        }
    }

    render() {
        return (
            <div ref="richEditor" className={["rich-editor", this.props.className, this.props.type].join(" ")} style={this.props.style}>
                <div ref="toolbox" className="toolbox"></div>
                <div ref="content" className="content"></div>
            </div>
        );
    }
}