import miniProgramRequest from './base';
import Controller from './controller';

class ArticleController extends Controller {
    constructor() {
        super("/article");
    }

    getAll(params) {
        return miniProgramRequest.get(this.resoursePath + "/all", {
            params
        });
    }

    togglePublish(id, isPublish) {
        return this.update(id, { isPublish: isPublish });
    }

    setCarousel(id, mapId, order) {
        return miniProgramRequest.post(this.resoursePath + "/carousel", {
            articleId: id,
            mapId,
            order
        });
    }

    removeCarousel(id) {
        return miniProgramRequest.delete(this.resoursePath + "/carousel/" + id);
    }

    setHeadNew(id, mapId) {
        return miniProgramRequest.post(this.resoursePath + "/headnew", {
            articleId: id,
            mapId
        });
    }
}

export default new ArticleController();