import React, { Component } from 'react';
import PropTypes from "prop-types";

import "./index.css";

export class Form extends Component {

    static defaultProps = {
        type: "default"
    }

    static propTypes = {
        type: PropTypes.oneOf(["default", "horizontal", "inline"]),
        children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.element), PropTypes.element])
    }

    render() {
        return (<div className={["form", this.props.type, this.props.className].join(" ")}>
            {this.props.children}
        </div>);
    }
}

export class FormItem extends Component {

    static propTypes = {
        for: PropTypes.string.isRequired,
        className: PropTypes.string,
        style: PropTypes.object
    }

    render() {
        return (
            <div className="form-group">
                <label className="form-title" htmlFor={this.props.for}>{this.props.title}</label>
                <div className={this.props.className} style={this.props.style}>{this.props.children}</div>
            </div>
        );
    }
}