function FilePicker() { };

FilePicker.multiple = false;

FilePicker.filetypes = [];

FilePicker.fileInput = (function () {
    var input = document.createElement("input");
    input.type = "file";
    input.onclick = function () {
        //清除原有文件
        this.value = "";
        console.log("filepicker is opened");
    };
    input.onchange = function () {
        console.log("filepicker is selected");
    };
    input.multiple = FilePicker.multiple;
    input.accept = FilePicker.filetypes.join(",");

    return input;
})();

/**
* 打开文件
* @param {any} data
* @return {Promise<FileList>}
*/
FilePicker.open = function (data) {
    var fileInput = this.fileInput;
    fileInput.multiple = data.multiple === undefined ? this.multiple : data.multiple;
    if (fileInput.accept !== undefined)
        fileInput.accept = data.filetypes === undefined ? this.filetypes.join(",") : data.filetypes.join(",");
    return new Promise(function (resolve, reject) {
        var getFile = function () {
            resolve(this.files);
            this.removeEventListener("change", getFile);
        };
        fileInput.addEventListener("change", getFile);
        fileInput.click();
    });
};

export default FilePicker;