import React, { Component } from 'react';
import PropTypes from "prop-types";

import Modal from '../Modal/Index.jsx';

import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';

import './index.css';

export default class CropModal extends Component {

    static defaultProps = {
        state: false,
    }

    static propTypes = {
        state: PropTypes.bool.isRequired,
        onStateChange: PropTypes.func,
        src: PropTypes.string.isRequired,
        type: PropTypes.string.isRequired,
        aspectRatio: PropTypes.number.isRequired,
        onImageCropped: PropTypes.func.isRequired
    }

    onCrop = () => {
        if (this.props.onImageCropped) {
            this.refs.cropper
                .getCroppedCanvas()
                .toBlob(this.props.onImageCropped, this.props.type, 0.9);
        }
    }

    render() {
        return (
            <Modal state={this.props.state} onStateChange={this.props.onStateChange} title="图片裁剪">
                <Cropper ref='cropper'
                    className="cropper modal-section"
                    src={this.props.src}
                    aspectRatio={this.props.aspectRatio}
                    viewMode={1}
                    movable={false}
                    zoomable={false}
                    guides={false} />
                {this.props.children ? (<div className="modal-section">{this.props.children}</div>) : undefined}
                <div className="modal-footer modal-section">
                    <button className="btn blue" onClick={this.onCrop}>确定</button>
                    <button className="btn default" onClick={e => this.props.onStateChange(false)}>取消</button>
                </div>
            </Modal>
        );
    }
}