import './extensions/canvas';

/**
    * @param {HTMLImageElement} img
    */
export default class ImageProcessor {
    constructor(img) {
        this.img = img;
    }
    slice(sliceHeight, exportType) {
        return new Promise(resolve => {
            this.img.onload = async function () {
                const canvas = document.createElement("canvas");
                const sliceArray = [];
                const imgWidth = this.width;
                const ctx = canvas.getContext("2d");
                canvas.width = imgWidth;
                const sliceCount = parseInt(this.height / sliceHeight);
                const remaind = this.height % sliceHeight;
                canvas.height = sliceHeight;
                
                for (let i = 0; i < sliceCount; i++) {
                    ctx.drawImage(this, 0, i * sliceHeight, imgWidth, sliceHeight, 0, 0, imgWidth, sliceHeight);
                    const blob = await canvas.toBlobAsync(exportType);
                    sliceArray.push(blob);
                }
                if (remaind !== 0) {
                    canvas.height = remaind;
                    ctx.drawImage(this, 0, sliceHeight * sliceCount, imgWidth, remaind, 0, 0, imgWidth, remaind);
                    const blob = await canvas.toBlobAsync(exportType);
                    sliceArray.push(blob);
                }
                return resolve(sliceArray);
            };
        });
    }
}