import React, { Component } from 'react';
import { Form, FormItem } from '../../components/Form/Index';

import setting from '../../appsettings.json';

import ControllerPan from '../../request/controllerPan';

import './index.css';
//import { request } from 'https';

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: ''
        }
    }

    login = () => {
        //这里的item就是account[x]的形参
        const account = setting.account.filter(item => item.username == this.state.username && item.password == this.state.password);
        if(account.length===0){
            alert("登录失败,用户名密码错误！");
        }
        else{
            sessionStorage.setItem("authorize",true);
            this.remoteLogin(this.state.username,this.state.password);//远程登录
            //this.props.history.push("/");
        }
    }

    loginRequest = ControllerPan.create("/login");

    remoteLogin = (username,password) => {
        let url = "http://pan.hoppo.com.cn/api/login"
        //let formData = new FormData();
        //formData.append("username",this.state.username);
        //formData.append("password",this.state.password);
        // fetch(url,{
        //     method:'POST',
        //     headers:{"Content-Type":"application/json;charset=UTF-8"},
        //     //headers:{"Content-Type":"application/json"},
        //     body:JSON.stringify({
        //         "username":"王家昱",
        //         "password":"WAng1994@"
        //     })
        // }).then(Response => Response.json()).then(function(res){
        //     console.log("shenme");
        //     if(res.errcode===100 ||res.errcode===0){
        //         this.props.history.push("/")
        //     }
        //     else{
        //         alert(res.errmsg);
        //     }
        // });
        username = username && username !== '' ? username : undefined;
        password = password && password !== '' ? password : undefined;
        const res = this.loginRequest.postParams({username,password});
        if(res.errcode===0 || res.errcode===100){
            this.props.history.push("/");
        }
        else{
            alert(res.errmsg);
        }
    }

    onChange = (prop) => {
        return (e) => {
            const changeProps = {};
            changeProps[prop] = e.currentTarget.value
            this.setState(changeProps);
        }
    }

    render() {
        return (
            <div>
                <div className="login-bg" style={{ backgroundImage: "url('http://cn.bing.com/az/hprichbg/rb/ElephantParade_ZH-CN13084036095_1920x1080.jpg')" }}></div>
                <div className="login-modal">
                    <Form type="horizontal">
                        <FormItem for="username" title="账户:">
                            <input type="text" className="input" id="username" name="username" placeholder="请输入账户" value={this.state.username} onChange={this.onChange("username")} />
                        </FormItem>
                        <FormItem for="password" title="密码:">
                            <input type="password" className="input" id="password" name="password" placeholder="请输入密码" value={this.state.password} onChange={this.onChange("password")} />
                        </FormItem>
                    </Form>
                    <button className="btn blue btn-login" onClick={this.login}>登录</button>
                </div>
            </div>
        );
    }
}