import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './index.css';

export default class Table extends Component {
    constructor(props) {
        super(props);
    }

    static defaultProps = {
        paging: false,
        pagingIndex: 1,
        pagingNumber: 10,
        total: 0,
        data: [],
        selectable: false,
        selectType: "cell",
        rowStyle: {}
    }

    static propTypes = {
        //paging info
        paging: PropTypes.bool,
        pagingIndex: PropTypes.number,
        pagingNumber: PropTypes.number,
        total: PropTypes.number,
        onPageChange: PropTypes.func,

        columns: PropTypes.array.isRequired,
        data: PropTypes.array,

        selectable: PropTypes.bool,
        selectType: PropTypes.oneOf(["cell", "row"]),
        onSelectChange: PropTypes.func,

        rowStyle: PropTypes.object
    }

    onSelect = (index) => {
        return (e) => {
            if (this.props.onSelectChange) {
                this.props.onSelectChange(index);
            }
        }
    }

    getTotalPage() {
        return parseInt(this.props.total / this.props.pagingNumber) + (this.props.total % this.props.pagingNumber === 0 ? 0 : 1);
    }

    onPageChange = (changeIndex) => {
        return (e) => {
            if (changeIndex > 0 && changeIndex <= this.getTotalPage()) {
                if (this.props.onPageChange)
                    this.props.onPageChange(changeIndex);
            }
        }
    }

    showPaging() {
        const totalPage = this.getTotalPage();
        const pagingComponent = [];
        for (let i = 1; i <= totalPage; i++) {
            pagingComponent.push((<a key={"page-" + i} href="javascript:;" onClick={this.onPageChange(i)} className={["link-pageIndex", i === this.props.pagingIndex ? "active" : ""].join(" ")}>{i}</a>));
        }
        return pagingComponent;
    }

    render() {
        return (<div>
            <table className="table">
                <thead>
                    <tr>
                        {this.props.selectable ? (<th style={{ width: "1.4rem" }}></th>) : null}
                        {this.props.columns.map(item => (
                            <th key={item.key} style={{ width: item.width, textAlign: item.align }}>{item.name}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>

                    {this.props.data.map((item, index) => (
                        <tr key={index} className={[this.props.selectType == "row" ? "row-select" : undefined].join(" ")} style={{ ...this.props.rowStyle }} onClick={this.props.selectType === "row" ? this.onSelect(index) : undefined}>
                            {this.props.selectable ? (<td style={{ textAlign: "center" }}><input type="checkbox" onChange={this.props.selectType == "cell" ? this.onSelect(index) : undefined} checked={item.selected} /></td>) : null}
                            {this.props.columns.map(column => (
                                <td key={index + "_" + column.key} style={{ textAlign: column.align }}>{item[column.key]}</td>
                            ))}
                        </tr>
                    ))}
                </tbody>
                <tfoot>
                    {this.props.paging ? (
                        <tr>
                            <td colSpan={this.props.columns.length}>
                                <div>
                                    <a href="javascript:;" className="link-pageIndex" onClick={this.onPageChange(this.props.pagingIndex - 1)}>&lt;&lt;</a>
                                    {this.showPaging()}
                                    <a href="javascript:;" className="link-pageIndex" onClick={this.onPageChange(this.props.pagingIndex + 1)}>&gt;&gt;</a>
                                </div>
                            </td>
                        </tr>
                    ) : undefined}
                </tfoot>
            </table>
        </div>);
    }
}