import React, { Component } from 'react';

import PageHead from '../../components/PageHead/Index';
import Toolbox from '../../components/Toolbox/Index';
import Table from '../../components/Table/Index';
import IconSpan from '../../components/IconSpan/Index';
import Toast from '../../components/Toast/Index';

import Controller from '../../request/controller';

export default class Brand extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            total: 0,
            columns: [{
                key: "name",
                name: "品牌名称"
            }, {
                key: "logo",
                name: "品牌logo",
                width: "5rem",
                align: "center"
            }, {
                key: "digest",
                name: "品牌简介"
            }, {
                key: "order",
                name: "序号"
            }]
        }
    }

    request = Controller.create("/brand");

    async componentDidMount() {
        await this.ajaxTableData();
    }

    async ajaxTableData() {
        const res = await this.request.getAll();
        this.setState({
            total: res.data.length,
            data: res.data,
        })
    }

    setTableData() {
        return this.state.data.map(item => {
            return {
                ...item,
                selected: item.selected == undefined ? false : item.selected,
                logo: (<img style={{ height: "2rem" }} src={"https://wx.rocabin.com/api/file/" + item.signId} />)
            }
        });
    }

    onSelectChange = (index) => {
        this.setState({
            data: this.state.data.map((item, itemIndex) => {
                if (itemIndex === index) {
                    item.selected = !item.selected;
                }
                else {
                    item.selected = false;
                }

                return item;
            })
        })
    }

    getSelected() {
        return this.state.data.filter(item => item.selected === true);
    }

    async showSuccess() {
        Toast.showToast(<IconSpan iconName="success" iconColor="success" text="操作成功" />, "white");
        this.ajaxTableData();
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    showSelectedError() {
        this.showError("请选择一项进行操作");
    }

    onCreate = () => {
        this.props.history.push("/brand/edit");
    }

    onEdit = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        this.props.history.push('/brand/edit/' + selected[0].id);
    }

    onDelete = async () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        await this.request.delete(selected[0].id);
        this.showSuccess();
    }

    onOpenSeries = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        this.props.history.push('/series?brandId=' + selected[0].id);
    }

    onOpenProduct = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        this.props.history.push('/product?brandId=' + selected[0].id);
    }

    render() {
        return (
            <div>
                <PageHead>品牌管理</PageHead>
                <Toolbox>
                    <button className="btn default" onClick={this.onCreate}>新增</button>
                    <button className="btn default" onClick={this.onEdit}>编辑</button>
                    <button className="btn default" onClick={this.onDelete}>删除</button>
                    <button className="btn default" onClick={this.onOpenSeries}>系列管理</button>
                    <button className="btn default" onClick={this.onOpenProduct}>产品管理</button>
                </Toolbox>
                <Table
                    total={this.state.total}
                    columns={this.state.columns}
                    data={this.setTableData()}
                    selectable
                    selectType="row"
                    onSelectChange={this.onSelectChange} />
            </div>
        );
    }
}