import React, { Component } from 'react';

import PageHead from '../../components/PageHead/Index';
import Toolbox from '../../components/Toolbox/Index';
import Table from '../../components/Table/Index';
import Toast from '../../components/Toast/Index';
import Icon from '../../components/Icon/Index';
import IconSpan from '../../components/IconSpan/Index';
import CropModal from '../../components/CropModal/Index';
import { Form, FormItem } from '../../components/Form/Index';

import FilePicker from '../../utils/filepicker';

import articleAction from '../../request/article';
import fileAction from '../../request/file';

export default class Article extends Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                key: "title",
                name: "文章名称"
            }, {
                key: "author",
                name: "作者"
            }, {
                key: "isHeadNew",
                name: "头条",
                align: "center",
                width: "2rem"
            }, {
                key: "isCarousel",
                name: "轮播图",
                align: "center",
                width: "3rem"
            }, {
                key: "carouselOrder",
                name: "轮播图序号",
                width: "5rem"
            }, {
                key: "from",
                name: "来源",
                width: "2rem",
                align: "center"
            }, {
                key: "isPublish",
                name: "发布",
                align: "center",
                width: "2rem"
            }, {
                key: "readCount",
                name: "阅读数量",
                width: "4rem"
            }],
            data: [],
            pagingIndex: 1,
            pagingNumber: 10,
            state: false,
            order: '0',
        }
    }
    /**
     * 获取文章数据
     * @param {number} pagingIndex 
     * @param {number} pagingNumber 
     */
    async ajaxArticleData(pagingIndex, pagingNumber) {
        const res = await articleAction.getAll({ pageNumber: pagingIndex, pageSize: pagingNumber });
        this.setState({
            ...res.data
        });
    }

    componentDidMount() {
        this.ajaxArticleData(this.state.pagingIndex, this.state.pagingNumber);
    }

    /**
     * 当选择发生变化
     * @param {number} index
     */
    onSelectChange = (index) => {
        this.setState({
            data: this.state.data.map((item, itemIndex) => {
                if (itemIndex === index) {
                    item.selected = !item.selected;
                }
                else {
                    item.selected = false;
                }

                return item;
            })
        })
    }
    /**
     * 当页面发送变化
     * @param {number} index
     */
    onPageChange = (index) => {
        this.setState({
            pagingIndex: index
        });
        this.ajaxArticleData(index, this.state.pagingNumber);
    }

    setTableData() {
        return this.state.data.map(item => {
            return {
                ...item,
                selected: item.selected == undefined ? false : item.selected,
                from: <Icon name={item.from === 1 ? "wechat" : "local"} color={item.from === 1 ? "success" : "info"} />,
                isHeadNew: item.isHeadNew ? <Icon name="true" color="success" /> : "",
                isCarousel: item.isCarousel ? <Icon name="true" color="success" /> : "",
                isPublish: <Icon name={item.isPublish ? "true" : "false"} color={item.isPublish ? "success" : "error"} />,
                carouselOrder: item.carouselOrder != null ? item.carouselOrder : ""
            }
        });
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    showSelectedError() {
        this.showError("请选择一项进行操作");
    }

    showSelectWeiXinError() {
        this.showError("该文章来源于微信,无法操作");
    }

    getSelected() {
        return this.state.data.filter(item => item.selected === true);
    }

    onCreate = () => {
        this.props.history.push('/article/edit');
    }

    onEdit = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        if (selected[0].from === 1) {
            this.showSelectWeiXinError();
            return;
        }
        this.props.history.push('/article/edit/' + selected[0].id);
    }

    onDelete = async () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        if (selected[0].from === 1) {
            this.showSelectWeiXinError();
            return;
        }

        await articleAction.delete(selected[0].id);
        this.actionSuccess();
    }

    onPublishChange = async () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        await articleAction.togglePublish(selected[0].id, !selected[0].isPublish);
        this.actionSuccess();
    }

    openFile = async (type) => {
        const files = await FilePicker.open({ multiple: false, filetypes: ["image/*"] });
        if (files[0].size > 1024 * 1024) {
            this.showError("请选择小于1M大小的文件");
            return;
        }

        this.setState({
            selectInfo: {
                blob: files[0],
                type: files[0].type,
                name: files[0].name,
            },
            state: true,
            modalType: type
        });
    }

    onSetHeadNew = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        this.openFile("headnew");
    }

    onCarouselChange = async () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        if (selected[0].isCarousel === false) {
            this.openFile("carousel");
        }
        else {
            await articleAction.removeCarousel(selected[0].id);
            this.actionSuccess();
        }
    }

    onStateChange = (state) => {
        this.setState({
            state
        });
    }

    onOrderChange = (e) => {
        this.setState({
            order: e.currentTarget.value
        });
    }

    setHeadOrCarousel = async (blob) => {
        const res = await fileAction.create(new File([blob], this.state.selectInfo.name, { type: this.state.selectInfo.type }));
        const id = this.getSelected()[0].id;
        this.state.modalType === "headnew" ?
            await articleAction.setHeadNew(id, res.data.body.id) :
            await articleAction.setCarousel(id, res.data.body.id, parseInt(this.state.order));

        this.setState({
            selectInfo: undefined,
            order: '0',
            state: false,
        });
        this.actionSuccess();
    }

    actionSuccess() {
        Toast.showToast(<IconSpan iconName="success" iconColor="success" text="操作成功" />, "white");
        this.ajaxArticleData(this.state.pagingIndex, this.state.pagingNumber);
    }

    render() {
        return (
            <div>
                <PageHead>文章管理</PageHead>
                <Toolbox>
                    <button className="btn default" onClick={this.onCreate}>新增</button>
                    <button className="btn default" onClick={this.onEdit}>编辑</button>
                    <button className="btn default" onClick={this.onDelete}>删除</button>
                    <button className="btn default" onClick={this.onPublishChange}>发布/取消</button>
                    <button className="btn default" onClick={this.onSetHeadNew}>设为头条</button>
                    <button className="btn default" onClick={this.onCarouselChange}>设为/取消轮播图</button>
                </Toolbox>
                <Table
                    paging
                    pagingIndex={this.state.pagingIndex}
                    pageNumber={this.state.pagingNumber}
                    onPageChange={this.onPageChange}
                    total={this.state.total}
                    columns={this.state.columns}
                    data={this.setTableData()}
                    selectable
                    selectType="row"
                    onSelectChange={this.onSelectChange} />

                {this.state.selectInfo ? (
                    <CropModal state={this.state.state}
                        onStateChange={this.onStateChange}
                        aspectRatio={this.state.modalType === "headnew" ? (3 / 4) : (16 / 9)}
                        src={window.URL.createObjectURL(this.state.selectInfo.blob)}
                        type={this.state.selectInfo.type}
                        onImageCropped={this.setHeadOrCarousel}>
                        <Form type="horizontal">
                            {this.state.modalType !== "headnew" ? (
                                <FormItem for="order" title="排序号:">
                                    <input id="order" className="input" type="number" value={this.state.order} onChange={this.onOrderChange} />
                                </FormItem>
                            ) : undefined}
                        </Form>

                    </CropModal>
                ) : null}

            </div>
        );
    }
}