import React from 'react';

import axios from 'axios';

import Toast from '../components/Toast/Index';
import IconSpan from '../components/IconSpan/Index';

const miniProgramRequest = axios.create({
    baseURL: "https://wx.rocabin.com/api",
});

miniProgramRequest.interceptors.response.use(res => {
    if (res.config.method.toLowerCase() === "get") {
        return res;
    }

    if (res.data.errorCode === 0) {
        return res;
    }
    else {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={res.data.message} />, "white");
        return Promise.reject(res.data.message);
    }
}, error => {
    Toast.showToast(<IconSpan iconName="error" iconColor="error" text="网络繁忙,请稍后重试" />, "white");
    return Promise.reject(error);
})

export default miniProgramRequest;

