import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom';

import Router from './Router';

import './style/app.css';

export default class App extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <BrowserRouter>
                <Router />
            </BrowserRouter>
        )
    }
}