import React, { Component } from 'react';

import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';
import PageHead from '../../components/PageHead/Index';

import Controller from '../../request/controller';
import fileAction from '../../request/file';

import FilePicker from '../../utils/filepicker';

import mime from 'mime';

export default class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            brandData: [],
            brandId: '',
            order: 0,
            colSpan: 1,
        }
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    seriesRequest = Controller.create("/series");
    brandRequest = Controller.create("/brand");

    async getFile(fileId, propName, fileName) {
        const res = await fileAction.get(fileId);
        const object = {};
        object[propName] = {
            blob: new Blob([res.data]),
            type: res.headers["content-type"],
            name: fileName + "." + mime.getExtension(res.headers["content-type"])
        }
        this.setState({
            ...object
        });
    }

    async getBrand() {
        const res = await this.brandRequest.getAll();
        this.setState({
            brandData: res.data
        });
    }

    async componentDidMount() {
        this.getBrand();
        const id = this.props.match.params.id;
        if (id) {
            const res = await this.seriesRequest.get(id);
            if (res.data !== null) {
                this.setState({
                    ...res.data
                });
                this.getFile(res.data.signId, "signInfo", "logo");
            }
            else {
                this.showError("数据加载失败");
            }
        }
    }

    onPropChange = (prop, value) => {
        const object = {};
        object[prop] = value;
        this.setState({
            ...object
        });
    }

    onSelectSign = async () => {
        const files = await FilePicker.open({ multiple: false, filetypes: ["image/*"] });
        if (files[0].size > 1024 * 1024) {
            this.showError("请选择小于1mb的图片");
        }
        this.setState({
            signInfo: {
                blob: files[0],
                name: files[0].name,
                type: files[0].type
            }
        })
    }

    /**
     * @returns {string}
     */
    uploadFile = async (propInfo) => {
        const res = await fileAction.create(new File([this.state[propInfo].blob], this.state[propInfo].name, { type: this.state[propInfo].type }));
        return res.data.body.id;
    }

    submit = async () => {
        if (!this.state.brandId || this.state.brandId === "") {
            this.showError("请选择品牌");
            return;
        }
        if (!this.state.signInfo) {
            this.showError("请选择展示图");
            return;
        }
        const signId = await this.uploadFile("signInfo");
        const brandId = this.state.brandId;
        const name = this.state.name;
        const order = this.state.order;
        const colSpan = this.state.colSpan;
        await this.seriesRequest.createOrUpdate(this.props.match.params.id, {
            brandId,
            name,
            order,
            colSpan,
            signId,
        });
        this.props.history.goBack();
    }

    goback = () => {
        this.props.history.goBack();
    }

    render() {
        return (
            <div>
                <PageHead goback history={this.props.history}>系列{this.props.match.params.id ? "编辑" : "创建"}</PageHead>
                <Form type="horizontal">
                    <FormItem for="brand" title="所属品牌:">
                        <select id="brand" className="select" value={this.state.brandId} onChange={e => this.onPropChange("brandId", e.currentTarget.value)}>
                            <option value="">==请选择==</option>
                            {this.state.brandData.map((item, index) => (
                                <option key={index} value={item.id}>{item.name}</option>
                            ))}
                        </select>
                    </FormItem>
                    <FormItem for="name" title="系列名称:">
                        <input id="name" className="input" type="text" placeholder="请输入系列名称" value={this.state.name} onChange={e => this.onPropChange("name", e.currentTarget.value)} />
                    </FormItem>
                    <FormItem for="order" title="系列序号:">
                        <input id="order" className="input" type="number" placeholder="请输入系列序号" value={this.state.order} onChange={e => this.onPropChange("order", parseInt(e.currentTarget.value))} />
                    </FormItem>
                    <FormItem for="sign" title="系列展示图:">
                        <button className="btn default" onClick={this.onSelectSign}>选择文件</button>&nbsp;&nbsp;
                        <span>{this.state.signInfo ? this.state.signInfo.name : "注：图片大小不允许大于1mb,支持格式jpg/png/gif"}</span>
                    </FormItem>
                    <FormItem for="colSpan" title="系列跨列数:">
                        <input className="input" type="radio" name="colSpan" value="1" checked={this.state.colSpan === 1} onChange={e => this.onPropChange("colSpan", parseInt(e.currentTarget.value))} />1
                        <input className="input" type="radio" name="colSpan" value="2" checked={this.state.colSpan === 2} onChange={e => this.onPropChange("colSpan", parseInt(e.currentTarget.value))} />2
                    </FormItem>
                </Form>
                <div className="page-main-footer">
                    <button className="btn blue" onClick={this.submit}>提交</button>&nbsp;&nbsp;&nbsp;
                    <button className="btn default" onClick={this.goback}>取消</button>
                </div>
            </div>
        );
    }
}