import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class Image extends Component {
    constructor(props) {
        super(props);
    }

    static propTypes = {
        src: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.instanceOf(Blob)
        ]),
    }

    shouldComponentUpdate(nextProps) {
        if (nextProps.src == this.props.src) {
            return false;
        }
        return true;
    }

    setImg() {
        if (this.props.src.constructor === Blob) {
            return window.URL.createObjectURL(this.props.src)
        }
        else {
            return this.props.src
        }
    }

    render() {
        return (<div className={this.props.className} onClick={this.props.onClick} style={{ ...this.props.style, content: `url('${this.setImg()}')` }}></div>);
    }
}