import React, { Component } from 'react';

import { Form, FormItem } from '../../components/Form/Index';
import PageHead from '../../components/PageHead/Index';
import RichEditor from '../../components/RichEditor/Index';
import CropModal from '../../components/CropModal/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';

import FilePicker from '../../utils/filepicker';

import fileAction from '../../request/file';
import articleAction from '../../request/article';

import mime from 'mime';

import './edit.css';

export default class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: '',
            digest: '',
            author: '',
            content: '',
            thumbId: undefined,

            state: false,
            selectInfo: undefined,
        }
    }

    async getFile(fileId, propName) {
        const res = await fileAction.get(fileId);
        const object = {};
        object[propName] = {
            blob: new Blob([res.data]),
            type: res.headers["content-type"],
            name: propName + "." + mime.getExtension(res.headers["content-type"])
        }
        this.setState({
            ...object
        });
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    async componentDidMount() {
        const id = this.props.match.params.id;
        if (id) {
            const res = await articleAction.get(id);
            this.setState({
                ...res.data,
            });
            if (res.data !== null) {
                this.getFile(res.data.thumbId, "selectInfo");
            }
        }
    }

    changeProp = (prop, value) => {
        const state = {};
        state[prop] = value;
        this.setState({
            ...state
        });
    }

    selectFile = async (e) => {
        const files = await FilePicker.open({ multiple: false, filetypes: ["image/*"] });
        if (files[0].size > 256 * 1024) {
            this.showError("图片大小不允许大于256kb");
            return;
        }
        this.setState({
            selectInfo: {
                blob: files[0],
                type: files[0].type,
                name: files[0].name
            },
            state: true
        });
    }

    onStateChange = (state) => {
        this.setState({
            state: state
        });
    }

    goback = () => {
        this.props.history.goBack();
    }

    saveCropData = (blob) => {
        this.setState({
            selectInfo: {
                ...this.state.selectInfo,
                blob: blob
            },
            state: false,
        });
    }

    uploadFile = async () => {
        if (this.state.selectInfo) {
            const res = await fileAction.create(new File([this.state.selectInfo.blob], this.state.selectInfo.name, { type: this.state.selectInfo.type }));
            return res.data.body.id;
        }
        else {
            if (this.state.thumbId) {
                return this.state.thumbId;
            }
            else {
                this.showError("请选择缩略图");
            }
        }
    }

    submit = async () => {
        const fileId = await this.uploadFile();
        if (fileId) {
            const model = {
                title: this.state.title,
                digest: this.state.digest,
                author: this.state.author,
                content: this.state.content,
                thumbId: fileId
            }
            await articleAction.createOrUpdate(this.props.match.params.id, model);
            this.props.history.goBack();
        }
    }

    onImageReceive = (insertImg, res) => {
        if (res.errorCode === 0) {
            insertImg("https://wx.rocabin.com/api/file/" + res.body.id);
        }
        else {
            this.showError(res.message);
        }
    }

    render() {
        return (
            <div>
                <PageHead goback history={this.props.history}>文章{this.props.match.params.id ? "编辑" : "创建"}</PageHead>
                <div className="page-main-body">
                    <Form type="horizontal">
                        <FormItem for="thumb" title="文章缩略图:">
                            <button className="btn default" onClick={this.selectFile}>选择文件</button>&nbsp;&nbsp;
                            <span>注：图片大小不允许大于256kb,支持格式jpg/png/gif</span>
                            {this.state.selectInfo ? (
                                <div className="cropper-perview-wapper">
                                    <img className="cropper-perview" src={window.URL.createObjectURL(this.state.selectInfo.blob)} onClick={e => this.onStateChange(true)} />
                                </div>
                            ) : null}
                            {this.state.selectInfo ? (
                                <CropModal
                                    state={this.state.state}
                                    onStateChange={this.onStateChange}
                                    aspectRatio={16 / 9}
                                    src={window.URL.createObjectURL(this.state.selectInfo.blob)}
                                    type={this.state.selectInfo.type}
                                    onImageCropped={this.saveCropData} />
                            ) : null}
                        </FormItem>
                        <FormItem for="title" title="文章标题:">
                            <input id="title" className="input" type="text" placeholder="请输入文章标题" value={this.state.title} onChange={e => this.changeProp("title", e.currentTarget.value)} />
                        </FormItem>
                        <FormItem for="digest" title="文章简介:">
                            <input id="digest" className="input" type="text" placeholder="请输入文章简介" value={this.state.digest} onChange={e => this.changeProp("digest", e.currentTarget.value)} />
                        </FormItem>
                        <FormItem for="author" title="文章作者:">
                            <input id="author" className="input" type="text" placeholder="请输入文章作者" value={this.state.author} onChange={e => this.changeProp("author", e.currentTarget.value)} />
                        </FormItem>
                        <FormItem for="content" title="文章正文:" style={{ flexGrow: 1 }}>
                            <RichEditor type="fit"
                                value={this.state.content}
                                uploadPath="https://wx.rocabin.com/api/file"
                                onImageReceive={this.onImageReceive}
                                onChange={value => this.changeProp("content", value)} />
                        </FormItem>
                    </Form>
                </div>
                <div className="page-main-footer">
                    <button className="btn blue" onClick={this.submit}>提交</button>&nbsp;&nbsp;&nbsp;
                    <button className="btn default" onClick={this.goback}>取消</button>
                </div>
            </div>
        );
    }
}