import miniProgramRequest from './base';

export default class Controller {
    /**
     * 
     * @param {string} resoursePath 
     */
    constructor(resoursePath) {
        this.resoursePath = resoursePath;
    }

    static create(path){
        return new Controller(path);
    }

    getAll(params) {
        console.log(params);
        return miniProgramRequest.get(this.resoursePath, { params });
    }

    get(id) {
        return miniProgramRequest.get(this.resoursePath + "/" + id);
    }

    create(model) {
        return miniProgramRequest.post(this.resoursePath, model);
    }

    update(id, model) {
        return miniProgramRequest.put(this.resoursePath + "/" + id, model);
    }

    createOrUpdate(id, model) {
        return id ? this.update(id, model) : this.create(model);
    }

    delete(id) {
        return miniProgramRequest.delete(this.resoursePath + "/" + id);
    }
}

