import React, { Component } from 'react';

import PageHead from "../../components/PageHead/Index";
import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';
import CropModal from '../../components/CropModal/Index';
import Image from '../../components/Image/Index';

import productAction from '../../request/product';
import fileAction from '../../request/file';
import Controller from '../../request/controller';

import FilePicker from '../../utils/filepicker';
import ImageProcessor from '../../utils/imageProcessor';
import mime from 'mime';

import './edit.css';

export default class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            state: false,
            brandData: [],
            seriesData: [],
            brandId: '',
            seriesId: '',
            sliceIntroduce: []
        }
    }

    async componentDidMount() {
        const id = this.props.match.params.id;
        this.ajaxBrandData();
        if (id) {
            const res = await productAction.get(id);
            if (res.data != null) {
                this.setState({
                    ...res.data
                });
                const regex = /https:\/\/wx.rocabin.com\/api\/file\/[^\"\']*/g;
                this.setState({
                    sliceIntroduce: res.data.introduce.match(regex)
                });

                this.getFile(res.data.thumbId, "thumbInfo", "thumb");
                this.ajaxSeriesData(res.data.brandId);
            }
        }
    }

    brandRequest = Controller.create("/brand");
    seriesRequest = Controller.create("/series");

    async ajaxBrandData() {
        const res = await this.brandRequest.getAll();
        this.setState({
            brandData: res.data
        });
    }

    async ajaxSeriesData(brandId) {
        if (brandId && brandId !== '') {
            const res = await this.seriesRequest.getAll({ brandId });
            this.setState({
                seriesData: res.data
            });
        }
        else {
            this.setState({
                seriesData: []
            })
        }
    }

    async getFile(fileId, propName, fileName) {
        const res = await fileAction.get(fileId);
        const object = {};
        object[propName] = {
            blob: new Blob([res.data]),
            type: res.headers["content-type"],
            name: fileName + "." + mime.getExtension(res.headers["content-type"])
        }
        this.setState({
            ...object
        });
    }

    onBrandChange = (e) => {
        this.setState({
            brandId: e.currentTarget.value,
            seriesId: ''
        });
        this.ajaxSeriesData(e.currentTarget.value);
    }

    onSeriesChange = (e) => {
        this.setState({
            seriesId: e.currentTarget.value
        });
    }

    onPropChange = (prop, value) => {
        const object = {};
        object[prop] = value;
        this.setState({
            ...object
        })
    }

    selectFile = async (e) => {
        const files = await FilePicker.open({ multiple: false, filetypes: ["image/*"] });
        const image = document.createElement("img");
        image.src = window.URL.createObjectURL(files[0]);
        const processor = new ImageProcessor(image);
        const array = await processor.slice(600, files[0].type);
        console.log(array);
        this.setState({
            fileInfo:{
                blob: files[0],
                type: files[0].type,
                name: files[0].name
            },
            fileName: files[0].name,
            sliceIntroduce: array
        });
    }

    onStateChange = (state) => {
        this.setState({
            state: state
        });
    }

    saveCropData = (blob) => {
        this.setState({
            thumbInfo: {
                ...this.state.thumbInfo,
                blob: blob
            },
            state: false,
        });
    }

    uploadFile = async (propInfo) => {
        const res = await fileAction.create(new File([this.state[propInfo].blob], this.state[propInfo].name, { type: this.state[propInfo].type }));
        return res.data.body.id;
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    submit = async () => {
        const thumbId = await this.uploadFile("thumbInfo");
        let introduce = this.state.introduce;
        if (this.state.introduceName) {
            introduce = "";
            for (let i = 0; i < this.state.sliceIntroduce.length; i++) {
                const res = await fileAction.create(new File([this.state.sliceIntroduce[i]], this.state.introduceName, { type: this.introduceType }));
                introduce += `<div style='width:100%;content:url("https://wx.rocabin.com/api/file/${res.data.body.id}");'></div>`;
            }
        }
        const brandId = this.state.brandId;
        const seriesId = this.state.seriesId;
        const code = this.state.code;
        const name = this.state.name;
        const alias = this.state.alias;
        const digest = this.state.digest;
        const retailPrice = this.state.retailPrice;
        const barcode = this.state.barcode;
        const permitAge = this.state.permitAge;
        const isPopular = false;
        const origin = this.state.origin;
        const manufacturer = this.state.manufacturer;
        const material = this.state.material;
        const isOn = false;
        await productAction.createOrUpdate(this.props.match.params.id, { brandId, seriesId, thumbId, code, name, alias, digest, retailPrice, introduce, barcode, permitAge, isPopular, origin, manufacturer, material, isOn });
        this.props.history.goBack();
    }

    goback = () => {
        this.props.history.goBack();
    }

    render() {
        return (
            <div>
                <PageHead goback history={this.props.history}>文件编辑</PageHead>
                <div className="form-content">
                    <div className="form-main">
                        <Form type="horizontal">
                            <FormItem for="introduce" title="选择文件:">
                                <button className="btn default" onClick={this.selectFile}>选择文件</button>
                                <span>{this.state.fileInfo ? this.state.fileInfo.name : "注：图片大小不允许大于256kb,支持格式png"}</span>
                            </FormItem>
                            <FormItem for="fileName" title="文件名称:">
                                <input id="fileName" type="text" className="input" placeholder="请输入文件名称" value={this.state.fileName} onChange={e => this.onPropChange("fileName", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="sharedUrl" title="分享链接:">
                                <input id="sharedUrl" type="text" className="input" placeholder="请输入分享链接" value={this.state.sharedUrl} onChange={e => this.onPropChange("sharedUrl", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="brandId" title="选择品牌:">
                                <select id="brandId" className="select" value={this.state.brandId} onChange={this.onBrandChange}>
                                    <option value=''>==请选择==</option>
                                    {this.state.brandData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                            <FormItem for="seriesId" title="选择系列:">
                                <select id="seriesId" className="select" value={this.state.seriesId} onChange={this.onSeriesChange}>
                                    <option value=''>==请选择==</option>
                                    {this.state.seriesData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                            <FormItem for="fileType" title="选择文件类型:">
                                <select id="fileType" className="select" value={this.state.filetype} onChange={this.onFileTypeChange}>
                                    <option value=''>==请选择==</option>
                                    {this.state.seriesData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                        </Form>
                        <div className="page-main-footer">
                            <button className="btn blue" onClick={this.submit}>提交</button>&nbsp;&nbsp;&nbsp;
                            <button className="btn default" onClick={this.goback}>取消</button>
                        </div>
                    </div>
                    <div className="form-preview" style={{ width: "24rem" }}>
                        <div className="preview">
                            {this.state.sliceIntroduce.map((item, index) => (
                                <Image key={index} style={{ width: "100%" }} src={item} />
                            ))}
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}