import React, { Component } from 'react';

import PageHead from '../../components/PageHead/Index';
import Toolbox from '../../components/Toolbox/Index';
import Table from '../../components/Table/Index';
import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';

import Controller from '../../request/controller';

import { getQueryString } from '../../utils/url';

export default class Series extends Component {
    constructor(props) {
        super(props);
        this.state = {
            brandData: [],
            brandId: getQueryString("brandId"),
            data: [],
            total: 0,
            columns: [{
                key: "name",
                name: "系列名称"
            }, {
                key: "order",
                name: "系列序号"
            }, {
                key: "colSpan",
                name: "系列跨列数"
            }]
        }
    }

    brandRequest = Controller.create("/brand");
    seriesRequest = Controller.create("/series");

    async ajaxBrandData() {
        const res = await this.brandRequest.getAll();
        this.setState({
            brandData: res.data
        });
    }

    async ajaxSeriesData(brandId) {
        if (brandId && brandId !== '') {
            const res = await this.seriesRequest.getAll({ brandId });
            this.setState({
                data: res.data,
                total: res.data.length,
            });
        }
        else {
            this.setState({
                data: [],
                total: 0
            })
        }
    }

    componentDidMount() {
        this.ajaxBrandData();
        this.ajaxSeriesData(this.state.brandId);
    }

    onSelectChange = (index) => {
        this.setState({
            data: this.state.data.map((item, itemIndex) => {
                if (itemIndex === index) {
                    item.selected = !item.selected;
                }
                else {
                    item.selected = false;
                }

                return item;
            })
        })
    }

    setTableData() {
        return this.state.data.map(item => {
            return {
                ...item,
                selected: item.selected == undefined ? false : item.selected
            }
        });
    }

    onBrandChange = (e) => {
        this.setState({
            brandId: e.currentTarget.value
        });
        this.ajaxSeriesData(e.currentTarget.value);
    }

    getSelected() {
        return this.state.data.filter(item => item.selected === true);
    }

    async showSuccess() {
        Toast.showToast(<IconSpan iconName="success" iconColor="success" text="操作成功" />, "white");
        this.ajaxSeriesData(this.state.brandId);
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    showSelectedError() {
        this.showError("请选择一项进行操作");
    }

    onCreate = () => {
        this.props.history.push("/series/edit");
    }

    onEdit = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        this.props.history.push("/series/edit/" + selected[0].id);
    }

    onDelete = async () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        await this.seriesRequest.delete(selected[0].id);
        this.showSuccess();
    }

    onOpenProduct = () => {
        const selected = this.getSelected();
        if (selected.length === 0) {
            this.showSelectedError();
            return;
        }
        this.props.history.push(`/product?brandId=${this.state.brandId}&seriesId=${selected[0].id}`);
    }

    render() {
        return (
            <div>
                <PageHead>系列管理</PageHead>
                <Form type="inline">
                    <FormItem for="brandId" title="选择品牌:">
                        <select id="brandId" className="select" value={this.state.brandId} onChange={this.onBrandChange}>
                            <option value=''>==请选择==</option>
                            {this.state.brandData.map((item, index) => (
                                <option key={index} value={item.id}>{item.name}</option>
                            ))}
                        </select>
                    </FormItem>
                </Form>
                <Toolbox>
                    <button className="btn default" onClick={this.onCreate}>新增</button>
                    <button className="btn default" onClick={this.onEdit}>编辑</button>
                    <button className="btn default" onClick={this.onDelete}>删除</button>
                    <button className="btn default" onClick={this.onOpenProduct}>产品管理</button>
                </Toolbox>
                <Table
                    total={this.state.total}
                    columns={this.state.columns}
                    data={this.setTableData()}
                    selectable
                    selectType="row"
                    onSelectChange={this.onSelectChange} />
            </div>
        );
    }
}