import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

import './index.css';

export default class Sidebar extends Component {
    render() {
        return (
            <div className="nav">
                <NavLink exact className="nav-item" to={`${this.props.path}`}>首页</NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/article`}>文章管理</NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/brand`}>品牌管理</NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/series`}>系列管理</NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/product`}>产品管理</NavLink>
                <NavLink exact className="nav-item" to={`${this.props.path}/file`}>文件管理</NavLink>
            </div>
        );
    }
}