import React, { Component } from 'react';

import PageHead from '../../components/PageHead/Index';
import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';

import Controller from '../../request/controller';
import fileAction from '../../request/file';

import FilePicker from '../../utils/filepicker';

import mime from 'mime';

import './edit.css';

export default class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            digest: '',
            order: 0,
        }
    }

    request = Controller.create('/brand');

    async getFile(fileId, propName, fileName) {
        const res = await fileAction.get(fileId);
        const object = {};
        object[propName] = {
            blob: new Blob([res.data]),
            type: res.headers["content-type"],
            name: fileName + "." + mime.getExtension(res.headers["content-type"])
        }
        this.setState({
            ...object
        });
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    async componentDidMount() {
        const id = this.props.match.params.id;
        if (id) {
            const res = await this.request.get(id);
            if (res.data !== null) {
                this.setState({
                    ...res.data
                });
                this.getFile(res.data.signId, "signInfo", "logo");
                this.getFile(res.data.backgroundId, "backgroundInfo", "background");
            }
            else {
                this.showError("数据加载失败");
            }
        }
    }

    onPropChange = (prop, value) => {
        const object = {};
        object[prop] = value;
        this.setState({
            ...object
        });
    }

    selectSignFile = async () => {
        const files = await FilePicker.open({ muitiple: false, filetypes: ["image/png"] });
        if (files[0].size > 256 * 1024) {
            this.showError("图片大小超出限制");
        }
        this.setState({
            signInfo: {
                blob: files[0],
                type: files[0].type,
                name: files[0].name
            }
        });
    }

    selectBackgroundFile = async () => {
        const files = await FilePicker.open({ muitiple: false, filetypes: ["image/*"] });
        if (files[0].size > 1024 * 1024) {
            this.showError("图片大小超出限制");
        }
        this.setState({
            backgroundInfo: {
                blob: files[0],
                type: files[0].type,
                name: files[0].name
            }
        });
    }

    /**
     * @returns {string}
     */
    uploadFile = async (propInfo) => {
        const res = await fileAction.create(new File([this.state[propInfo].blob], this.state[propInfo].name, { type: this.state[propInfo].type }));
        return res.data.body.id;
    }

    submit = async () => {
        if (!this.state.signInfo) {
            this.showError("请选择logo图片");
            return;
        }
        if (!this.state.backgroundInfo) {
            this.showError("请选择背景图片");
            return;
        }
        const signTask = this.uploadFile("signInfo");
        const backgroundTask = this.uploadFile("backgroundInfo");
        const [signId, backgroundId] = await Promise.all([signTask, backgroundTask]);
        const name = this.state.name;
        const digest = this.state.digest;
        const order = this.state.order;
        await this.request.createOrUpdate(this.props.match.params.id, {
            name, digest, order, signId, backgroundId
        });
        this.props.history.goBack();
    }

    goback = () => {
        this.props.history.goBack();
    }

    render() {
        return (
            <div>
                <PageHead goback history={this.props.history}>品牌{this.props.match.params.id ? "编辑" : "创建"}</PageHead>
                <div className="form-content">
                    <Form type="horizontal" className="form-main">
                        <FormItem for="name" title="品牌名称:">
                            <input id="name" type="text" className="input" value={this.state.name} placeholder="请输入品牌名称" onChange={e => this.onPropChange("name", e.currentTarget.value)} />
                        </FormItem>
                        <FormItem for="digest" title="品牌简介:">
                            <input id="digest" type="text" className="input" value={this.state.digest} placeholder="请输入品牌简介" onChange={e => this.onPropChange("digest", e.currentTarget.value)} />
                        </FormItem>
                        <FormItem for="order" title="品牌序号:">
                            <input id="order" type="number" className="input" value={this.state.order} placeholder="请输入品牌序号" onChange={e => this.onPropChange("order", parseInt(e.currentTarget.value))} />
                        </FormItem>
                        <FormItem for="order" title="品牌logo:">
                            <button className="btn default" onClick={this.selectSignFile}>选择文件</button>&nbsp;&nbsp;
                            <span>{this.state.signInfo ? this.state.signInfo.name : "注：图片大小不允许大于256kb,支持格式png"}</span>
                        </FormItem>
                        <FormItem for="order" title="品牌背景图:">
                            <button className="btn default" onClick={this.selectBackgroundFile}>选择文件</button>&nbsp;&nbsp;
                            <span>{this.state.backgroundInfo ? this.state.backgroundInfo.name : "注：图片大小不允许大于1mb,支持格式jpg/png/gif"}</span>
                        </FormItem>
                    </Form>
                    <div className="form-preview" style={{ width: "8rem" }}>
                        {this.state.backgroundInfo && this.state.signInfo ? (
                            <div className="brand" style={{ backgroundImage: "url('" + window.URL.createObjectURL(this.state.backgroundInfo.blob) + "')" }}>
                                <div className="brand-logo-wapper">
                                    <div className="brand-logo-background">
                                        <div className="brand-logo" style={{ content: "url('" + window.URL.createObjectURL(this.state.signInfo.blob) + "')" }}></div>
                                    </div>
                                </div>
                                <div className="brand-info">
                                    <div className="brand-name">{this.state.name}</div>
                                    <div className="brand-digest">-{this.state.digest}-</div>
                                </div>
                            </div>
                        ) : undefined}
                    </div>
                </div>
                <div className="page-main-footer">
                    <button className="btn blue" onClick={this.submit}>提交</button>&nbsp;&nbsp;&nbsp;
                    <button className="btn default" onClick={this.goback}>取消</button>
                </div>
            </div>
        );
    }
}