import React, { Component } from 'react'

export default class Icon extends Component {

    isHexColor() {
        return this.props.color.indexOf("#") != -1;
    }

    render() {
        return (
            <i className={["iconfont", "icon-" + this.props.name, this.isHexColor() ? "" : this.props.color].join(" ")}
                style={{ color: this.isHexColor() ? this.props.color : undefined, fontSize: this.props.size }} />
        );
    }
}