/**
 * 
 * @param {string} type 
 * @param {any[]} argument 
 * @return {Promise<Blob>}
 */
HTMLCanvasElement.prototype.toBlobAsync = function (type = "image/png", ...argument) {
    return new Promise((resolve, reject) => {
        try {
            this.toBlob(resolve, type, argument);
        }
        catch (e) {
            reject(e);
        }
    });
}