import React, { Component } from 'react';

import PageHead from "../../components/PageHead/Index";
import { Form, FormItem } from '../../components/Form/Index';
import Toast from '../../components/Toast/Index';
import IconSpan from '../../components/IconSpan/Index';
import CropModal from '../../components/CropModal/Index';
import Image from '../../components/Image/Index';

import productAction from '../../request/product';
import fileAction from '../../request/file';
import Controller from '../../request/controller';

import FilePicker from '../../utils/filepicker';
import ImageProcessor from '../../utils/imageProcessor';
import mime from 'mime';

import './edit.css';

export default class Edit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            state: false,
            brandData: [],
            seriesData: [],
            brandId: '',
            seriesId: '',
            sliceIntroduce: []
        }
    }

    async componentDidMount() {
        const id = this.props.match.params.id;
        this.ajaxBrandData();
        if (id) {
            const res = await productAction.get(id);
            if (res.data != null) {
                this.setState({
                    ...res.data
                });
                const regex = /https:\/\/wx.rocabin.com\/api\/file\/[^\"\']*/g;
                this.setState({
                    sliceIntroduce: res.data.introduce.match(regex)
                });

                this.getFile(res.data.thumbId, "thumbInfo", "thumb");
                this.ajaxSeriesData(res.data.brandId);
            }
        }
    }

    brandRequest = Controller.create("/brand");
    seriesRequest = Controller.create("/series");

    async ajaxBrandData() {
        const res = await this.brandRequest.getAll();
        this.setState({
            brandData: res.data
        });
    }

    async ajaxSeriesData(brandId) {
        if (brandId && brandId !== '') {
            const res = await this.seriesRequest.getAll({ brandId });
            this.setState({
                seriesData: res.data
            });
        }
        else {
            this.setState({
                seriesData: []
            })
        }
    }

    async getFile(fileId, propName, fileName) {
        const res = await fileAction.get(fileId);
        const object = {};
        object[propName] = {
            blob: new Blob([res.data]),
            type: res.headers["content-type"],
            name: fileName + "." + mime.getExtension(res.headers["content-type"])
        }
        this.setState({
            ...object
        });
    }

    onBrandChange = (e) => {
        this.setState({
            brandId: e.currentTarget.value,
            seriesId: ''
        });
        this.ajaxSeriesData(e.currentTarget.value);
    }

    onSeriesChange = (e) => {
        this.setState({
            seriesId: e.currentTarget.value
        });
    }

    onPropChange = (prop, value) => {
        const object = {};
        object[prop] = value;
        this.setState({
            ...object
        })
    }

    selectFile = async (e) => {
        const files = await FilePicker.open({ multiple: false, filetypes: ["image/*"] });
        if (files[0].size > 256 * 1024) {
            this.showError("图片大小不允许大于256kb");
            return;
        }
        this.setState({
            thumbInfo: {
                blob: files[0],
                type: files[0].type,
                name: files[0].name
            },
            state: true
        });
    }

    onStateChange = (state) => {
        this.setState({
            state: state
        });
    }

    saveCropData = (blob) => {
        this.setState({
            thumbInfo: {
                ...this.state.thumbInfo,
                blob: blob
            },
            state: false,
        });
    }

    selectIntroduce = async () => {
        const files = await FilePicker.open({ multiple: false, filetypes: ["image/*"] });
        const image = document.createElement("img");
        image.src = window.URL.createObjectURL(files[0]);
        const processor = new ImageProcessor(image);
        const array = await processor.slice(600, files[0].type);
        console.log(array);
        this.setState({
            introduceName: files[0].name,
            introduceType: files[0].type,
            sliceIntroduce: array
        });
    }

    uploadFile = async (propInfo) => {
        const res = await fileAction.create(new File([this.state[propInfo].blob], this.state[propInfo].name, { type: this.state[propInfo].type }));
        return res.data.body.id;
    }

    showError(text) {
        Toast.showToast(<IconSpan iconName="error" iconColor="error" text={text} />, "white");
    }

    submit = async () => {
        const thumbId = await this.uploadFile("thumbInfo");
        let introduce = this.state.introduce;
        if (this.state.introduceName) {
            introduce = "";
            for (let i = 0; i < this.state.sliceIntroduce.length; i++) {
                const res = await fileAction.create(new File([this.state.sliceIntroduce[i]], this.state.introduceName, { type: this.introduceType }));
                introduce += `<div style='width:100%;content:url("https://wx.rocabin.com/api/file/${res.data.body.id}");'></div>`;
            }
        }
        const brandId = this.state.brandId;
        const seriesId = this.state.seriesId;
        const code = this.state.code;
        const name = this.state.name;
        const alias = this.state.alias;
        const digest = this.state.digest;
        const retailPrice = this.state.retailPrice;
        const barcode = this.state.barcode;
        const permitAge = this.state.permitAge;
        const isPopular = false;
        const origin = this.state.origin;
        const manufacturer = this.state.manufacturer;
        const material = this.state.material;
        const isOn = false;
        await productAction.createOrUpdate(this.props.match.params.id, { brandId, seriesId, thumbId, code, name, alias, digest, retailPrice, introduce, barcode, permitAge, isPopular, origin, manufacturer, material, isOn });
        this.props.history.goBack();
    }

    goback = () => {
        this.props.history.goBack();
    }

    render() {
        return (
            <div>
                <PageHead goback history={this.props.history}>产品{this.props.match.params.id ? "编辑" : "创建"}</PageHead>
                <div className="form-content">
                    <div className="form-main">
                        <Form type="horizontal">
                            <FormItem for="brandId" title="所属品牌:">
                                <select id="brandId" className="select" value={this.state.brandId} onChange={this.onBrandChange}>
                                    <option value=''>==请选择==</option>
                                    {this.state.brandData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                            <FormItem for="seriesId" title="所属系列:">
                                <select id="seriesId" className="select" value={this.state.seriesId} onChange={this.onSeriesChange}>
                                    <option value=''>==请选择==</option>
                                    {this.state.seriesData.map((item, index) => (
                                        <option key={index} value={item.id}>{item.name}</option>
                                    ))}
                                </select>
                            </FormItem>
                            <FormItem for="code" title="产品型号:">
                                <input id="code" type="text" className="input" placeholder="请输入产品型号" value={this.state.code} onChange={e => this.onPropChange("code", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="thumb" title="产品展示图:">
                                <button className="btn default" onClick={this.selectFile}>选择文件</button>&nbsp;&nbsp;<span>注：图片大小不允许大于256kb,支持格式jpg/png/gif</span>
                                {this.state.thumbInfo ? (
                                    <div className="cropper-perview-wapper">
                                        <Image className="cropper-perview" src={this.state.thumbInfo.blob} onClick={e => this.onStateChange(true)} />
                                    </div>
                                ) : null}
                                {this.state.thumbInfo ? (
                                    <CropModal
                                        state={this.state.state}
                                        onStateChange={this.onStateChange}
                                        aspectRatio={1}
                                        src={window.URL.createObjectURL(this.state.thumbInfo.blob)}
                                        type={this.state.thumbInfo.type}
                                        onImageCropped={this.saveCropData} />
                                ) : null}
                            </FormItem>
                            <FormItem for="name" title="产品名称:">
                                <input id="name" type="text" className="input" placeholder="请输入产品名称" value={this.state.name} onChange={e => this.onPropChange("name", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="alias" title="产品别名:">
                                <input id="alias" type="text" className="input" placeholder="请输入产品别名" value={this.state.alias} onChange={e => this.onPropChange("alias", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="digest" title="产品简介:">
                                <input id="digest" type="text" className="input" placeholder="请输入产品简介" value={this.state.digest} onChange={e => this.onPropChange("digest", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="retailPrice" title="建议零售价:">
                                <input id="retailPrice" type="text" className="input" placeholder="请输入建议零售价" value={this.state.retailPrice} onChange={e => this.onPropChange("retailPrice", parseInt(e.currentTarget.value))} />
                            </FormItem>
                            <FormItem for="barcode" title="产品编码:">
                                <input id="barcode" type="text" className="input" placeholder="请输入产品编码(条形码)" value={this.state.barcode} onChange={e => this.onPropChange("barcode", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="permitAge" title="限制年龄:">
                                <input id="permitAge" type="text" className="input" placeholder="请输入限制年龄" value={this.state.permitAge} onChange={e => this.onPropChange("permitAge", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="origin" title="产品产地:">
                                <input id="origin" type="text" className="input" placeholder="请输入产品产地" value={this.state.origin} onChange={e => this.onPropChange("origin", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="manufacturer" title="产品生产商:">
                                <input id="manufacturer" type="text" className="input" placeholder="请输入产品生产商" value={this.state.manufacturer} onChange={e => this.onPropChange("manufacturer", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="material" title="产品材质:">
                                <input id="material" type="text" className="input" placeholder="请输入产品材质" value={this.state.material} onChange={e => this.onPropChange("material", e.currentTarget.value)} />
                            </FormItem>
                            <FormItem for="introduce" title="产品详情页:">
                                <button className="btn default" onClick={this.selectIntroduce}>选择文件</button>
                            </FormItem>
                        </Form>
                        <div className="page-main-footer">
                            <button className="btn blue" onClick={this.submit}>提交</button>&nbsp;&nbsp;&nbsp;
                            <button className="btn default" onClick={this.goback}>取消</button>
                        </div>
                    </div>
                    <div className="form-preview" style={{ width: "24rem" }}>
                        <div className="preview">
                            {this.state.sliceIntroduce.map((item, index) => (
                                <Image key={index} style={{ width: "100%" }} src={item} />
                            ))}
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}