import miniProgramRequest from './base';
import Controller from './controller';

class ArticleController extends Controller {
    constructor() {
        super("/product");
    }

    setOn(id, isOn) {
        return this.update(id, { isOn });
    }

    setPopular(id, isPopular) {
        return this.update(id, { isPopular });
    }
    /**
     * 
     * @param {string} id 
     * @param {Array} showIds 
     */
    addShow(id, ...showIds) {
        return miniProgramRequest.post(this.resoursePath + "/show/" + id, showIds);
    }
    /**
     * 
     * @param {string} id 
     * @param {string[]} showIds 
     */
    removeShow(id, ...showIds) {
        return miniProgramRequest.delete(this.resoursePath + "/show/" + id, showIds);
    }
}

export default new ArticleController();