import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import './index.css';

export default class Toast extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isShow: false
        };
    }

    static defaultProps = {
        type: "black"
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({
                isShow: true
            });
        }, 10);
    }

    static removeElement(ele) {
        ReactDOM.unmountComponentAtNode(ele);
        document.body.removeChild(ele);
    }

    static showToast(content, type, duration = 2000) {
        const renderEle = document.createElement("div");
        document.body.appendChild(renderEle);
        ReactDOM.render((<Toast type={type}>{content}</Toast>), renderEle);
        //设置元素消失时间
        setTimeout(() => this.removeElement(renderEle), duration);
    }

    static showLoading(content, type) {
        const renderEle = document.createElement("div");
        document.body.appendChild(renderEle);
        ReactDOM.render((<Toast type={type}>{content}</Toast>), renderEle);
        return renderEle;
    }

    static hideLoading(ele) {
        this.removeElement(ele);
    }

    render() {
        return (<div className={["toast", this.state.isShow ? "show" : undefined, this.props.type].join(" ")}>{this.props.children}</div>);
    }
}

