import React, { Component } from 'react';
import PropTypes from 'prop-types';

import style from './index.css';

export default class Modal extends Component {
    constructor(props) {
        super(props);
    }

    static defaultProps = {
        state: false,
    }

    static propTypes = {
        state: PropTypes.bool.isRequired,
        onStateChange: PropTypes.func,
        title: PropTypes.string
    }

    closeModal = () => {
        if (this.props.onStateChange) {
            this.props.onStateChange(false);
        }
    }

    render() {
        return (
            <div className={["modal-shade", this.props.state ? "open" : "close"].join(" ")}>
                <div className="modal">
                    <div className="modal-head modal-container modal-section">
                        <div className="modal-title">{this.props.title}</div>
                        <button className="modal-close" onClick={this.closeModal}>×</button>
                    </div>
                    <div className="modal-container modal-section">{this.props.children}</div>
                </div>
            </div >
        );
    }
}