import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

// import Home from './pages/Home/Index';
// import Article from './pages/Article/Index';
// import ArticleEdit from './pages/Article/Edit';
// import Brand from './pages/Brand/Index';
// import BrandEdit from './pages/Brand/Edit';
// import Series from './pages/Series/Index';
// import SeriesEdit from './pages/Series/Edit';
// import Product from './pages/Product/Index';
// import ProductEdit from './pages/Product/Edit';

import asyncComponent from "../components/AsyncComponent/Index";
import Sidebar from '../components/Sidebar/Index';

export default class ManageRouter extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        const auth = sessionStorage.getItem("authorize");
        if (!auth || auth === false) {
            this.props.history.push("/login");
        }
    }

    getPath() {
        return this.props.match.url === "/" ? "" : this.props.match.url;
    }

    render() {
        return (
            <div className="page">
                <div className="page-sidebar page-item">
                    <Sidebar path={this.getPath()} />
                </div>
                <div className="page-main page-item">
                    <Switch>
                        <Route exact path={`${this.getPath()}/`} component={asyncComponent(() => require('../pages/Home/Index'))} />
                        <Route exact path={`${this.getPath()}/article`} component={asyncComponent(() => require('../pages/Article/Index'))} />
                        <Route exact path={`${this.getPath()}/article/edit/:id?`} component={asyncComponent(() => require('../pages/Article/Edit'))} />
                        <Route exact path={`${this.getPath()}/brand`} component={asyncComponent(() => require('../pages/Brand/Index'))} />
                        <Route exact path={`${this.getPath()}/brand/edit/:id?`} component={asyncComponent(() => require('../pages/Brand/Edit'))} />
                        <Route exact path={`${this.getPath()}/series`} component={asyncComponent(() => require('../pages/Series/Index'))} />
                        <Route exact path={`${this.getPath()}/series/edit/:id?`} component={asyncComponent(() => require('../pages/Series/Edit'))} />
                        <Route exact path={`${this.getPath()}/product`} component={asyncComponent(() => require('../pages/Product/Index'))} />
                        <Route exact path={`${this.getPath()}/product/edit/:id?`} component={asyncComponent(() => require('../pages/Product/Edit'))} />
                        <Route exact path={`${this.getPath()}/file`} component={asyncComponent(() => require('../pages/File/Index'))} />
                        <Route exact path={`${this.getPath()}/file/edit/:id?`} component={asyncComponent(() => require('../pages/File/Edit'))} />
                        <Route exact path={`${this.getPath()}/file/upload/:id?`} component={asyncComponent(() => require('../pages/File/Upload'))} />
                        <Route exact path={`${this.getPath()}/file/multiUpload/:id?`} component={asyncComponent(() => require('../pages/File/MultiUpload'))} />
                    </Switch>
                </div>
            </div>
        )
    }
}